th, td {
    padding: 20px;
}

.activity {
    width: 100%;
    margin: 0;
    margin-top: 5%;
    background-color: rgb(229, 229, 237);
    padding-bottom: 4px;   
}

.h4 {
    padding: 5% 5% 3% 5%;
    color: white;
    width: 90%;
    background: rgb(213, 209, 209);
}


table {
    border-collapse: collapse;
    width: 100%;
    text-align: left;
    border: 1px solid black;
    table-layout: fixed;
    margin: 0 auto;
    margin-bottom: 2px;
    box-shadow: 2px 2px 5px 5px white;
}


.tabble th ,td {
    
    font-weight: bold;
    font-size: 1.3em;
    width:100%;
}

@media (max-width: 400px) {
    .activity {
        width: 100%;
        margin-top: 8%; 
}
}