*{
    margin: 0;
    padding: 0;
    font-family: helvetica;
}

.header {
    vertical-align: middle;
}

.logo {
    margin: 10px;
    border: 1px solid black;
    display: inline-block;
    padding: 5px 40px;
    vertical-align: middle;
}

.notificationContainer {
    display: inline-block;
    margin: 10px 0px;
    vertical-align: middle;
    position: absolute;
    right: 0;    
}

.avatar {
    padding: 5px;
    display: inline-block;
}

.notification {
    display: inline-block;
    padding: 5px;
    margin: 0 10px;
}

.walletBalance {
    text-align: center;
    margin: 10px 2px;
    background-color: rgba(248, 248, 255, 0.994);
    border-radius: 30px;
    border: 1px solid rgba(204, 198, 198, 0.885);
    box-shadow: 2px 2px 8px;
}

.walletBalance h2 {
    margin: 20px;
    font-size: 22px;
    font-weight: bolder;
}
