.currency {
    margin: 30px 0 15px 0;
    padding: 25px 15px 5px 15px;
    background-color: rgb(236, 236, 247);
    color: rgb(67, 179, 223);
    font-size: 18px;
}

.token {
    background-color: lavender;
    margin: 0 5px;
    border-radius: 5px;
    border: 1px solid grey;
}

label {
    font-weight: bold;
    font-size: 22px;
    border: 1px solid black;
    display: inline-block;
    vertical-align: middle;
    padding: 5px;
}

input {
    padding: 7px;
    vertical-align: middle;
    border: none;
    outline: none;
    background: transparent;
    width: 70%;
    font-size: 18px;
    font-weight: bold;
}

.button {
    display: flex;
    justify-content: center;
    margin-top: 8%;
    outline: none;
}

button {
    width: 70%;
    margin: 0 5%;
    padding: 18px;
    border-radius: 10px;
    color: white;
    font-size: 20px;
    background-color: rgb(67, 179, 223);
    border: none;
}

@media (max-width: 400px) {
    label {
    font-weight: bold;
    font-size: 1.3em;
    }

    input {
    padding: 3px;
    width: 75%;
    }

}
